h1,
h2,
p,
ul,
li {
  margin: 0;
  padding: 0;
}

body {
  box-sizing: border-box;
  height: 100vh;
}

html {
  font-family: "Noto Serif", serif;
}

.App {
  padding: 20px;
  &__title {
    font-size: 40px;
    text-align: center;
    font-weight: 700;
  }

  &__wrapper {
    margin: auto;
    width: 500px;
    border: 1px solid black;
  }

  &__start-button {
    text-align: center;
    display: inline-block;
  }
}
